export const filterArrayOfObjects = <T>(arr: T[]) => {
  const withoutUndefined = arr.filter(item => item !== undefined);
  return [
    ...(new Map(
      withoutUndefined.map((item: T, key) => [
        item[key as unknown as keyof T],
        item,
      ]),
    ).values() as any),
    //   any,    stackoverflow ,    TS-    -  
  ];
};
